import React from 'react';
import { useForm, ValidationError } from '@formspree/react';

import Layout from "../components/layouts/layout"
import Container from "../components/container"
import Section from "../components/section"
import Seo from "../components/seo"

function SupportForm() {
  const [state, handleSubmit] = useForm("mrgdrvnr");
  if (state.succeeded) {
      return (
        <Layout title={"Helfer"}>
          <Section>
            <Container>
              <p>Vielen Dank für deine Unterstützung!</p>
            </Container>
          </Section>
        </Layout>
      )
  }
  return (
    <Layout title={"Helfende Hände gesucht"}>
      <Section>
        <Container>
          <div className="content">
            <p>
              Damit wir den Betrieb in unserem Verein gewährleisten können, sind wir immer wieder froh, auf helfende Hände zurückgreifen zu können.
            </p>
            <p>Kannst du dir vorstellen…</p>
            <ul>
              <li>… das Beizli einmal zu führen?</li>
              <li>… an einem Animationsturnier als Schiri aktiv zu sein? Bsp. am 11.12.22</li>
              <li>… an einem Animationsturnier als Zeitnehmer aktiv zu sein? Bsp. am 11.12.22</li>
              <li>… uns auf eine andere Art und Weise zu unterstützen?</li>
            </ul>
            <p>Dann fülle das Formular aus und schicke es ab. Wir werden uns dann bei dir melden.</p>
            <p>Herzlichen Dank</p>
            <p>TV Zofingen Handball</p>
          </div>

          <form onSubmit={handleSubmit}>
          <div className="field is-horizontal">
            <div className="field-label is-normal">
              <label className="label" htmlFor="email">Email Adresse</label>
            </div>
            <div className="field-body">
              <div className="field">
                <div className="control" >
                  <input
                    className="input"
                    id="email"
                    type="email"
                    name="email"
                    placeholder="Email Adresse"
                    />
                </div>
              </div>
            </div>
          </div>
          <ValidationError
            prefix="Email"
            field="email"
            errors={state.errors}
          />

          <div className="field is-horizontal">
            <div className="field-label is-normal">
              <label className="label" htmlFor="beizli">Unterstützung</label>
            </div>
            <div className="field-body">
              <div className="field">
                <div className="control">
                  <input type="checkbox" id="beizli" name="beizli"/>
                    Beizli
                </div>
                <div className="control">
                  <input type="checkbox" id="schiri" name="schiri"/>
                  Schiedsrichter:in an Animationsturnier
                </div>
                <div className="control">
                  <input type="checkbox" id="zeitnehmer" name="zeitnehmer"/>
                  Zeitnehmer:in an Animationsturnier
                </div>
                <div className="control">
                  <input type="checkbox" id="flexibel" name="flexibel"/>
                  Ich bin flexibel
                </div>
              </div>
            </div>
          </div>
          <ValidationError
              prefix="Beizli"
              field="beizli"
              errors={state.errors}
          />

          <div className="field is-horizontal">
            <div className="field-label is-normal">
              <label className="label" htmlFor="email">Nachricht</label>
            </div>
            <div className="field-body">
              <div className="field">
                <textarea
                  className="textarea"
                  id="message"
                  name="message"
                  placeholder="Deine Nachricht"
                />
              </div>
            </div>
          </div>
          <ValidationError
            prefix="Message"
            field="message"
            errors={state.errors}
          />

          <div className="field is-horizontal">
            <div className="field-label">
            </div>
            <div className="field-body">
              <div className="field">
                <div className="control">
                  <button type="submit" disabled={state.submitting}>
                    Abschicken
                  </button>
                </div>
              </div>
            </div>
          </div>

        </form>
        </Container>
      </Section>
    </Layout>
  );
}
function App() {
  return (
    <SupportForm />
  );
}
export default App;

export const Head = () => <Seo title="Kontakt" />
